import axios from "axios";
import categoryData from "./categoryData.json";

const API_KEY = process.env.REACT_APP_API_KEY;
const DB_NAME = "CategoryDB";
const STORE_NAME = "categories";

/**
 * IndexedDB  
 * @param {string} key -   (cat1-cat2-cat3)
 * @returns {Promise<string|null>}   ( null)
 */
async function getFromIndexedDB(key) {
  return new Promise((resolve, reject) => {
    const request = indexedDB.open(DB_NAME, 1);

    request.onupgradeneeded = (event) => {
      const db = event.target.result;
      if (!db.objectStoreNames.contains(STORE_NAME)) {
        db.createObjectStore(STORE_NAME);
      }
    };

    request.onsuccess = (event) => {
      const db = event.target.result;
      const transaction = db.transaction(STORE_NAME, "readonly");
      const store = transaction.objectStore(STORE_NAME);
      const getRequest = store.get(key);

      getRequest.onsuccess = () => resolve(getRequest.result || null);
      getRequest.onerror = () => reject(" IndexedDB   ");
    };

    request.onerror = () => reject(" IndexedDB  ");
  });
}

/**
 * IndexedDB  
 * @param {string} key -   (cat1-cat2-cat3)
 * @param {string} value -  
 */
async function saveToIndexedDB(key, value) {
  const request = indexedDB.open(DB_NAME, 1);

  request.onsuccess = (event) => {
    const db = event.target.result;
    const transaction = db.transaction(STORE_NAME, "readwrite");
    const store = transaction.objectStore(STORE_NAME);
    store.put(value, key);
  };
}

/**
 * JSON/IndexedDB   ,  API   IndexedDB 
 * @param {string} cat1 -  
 * @param {string} cat2 -  
 * @param {string} cat3 -  
 * @returns {Promise<string>}  
 */
export async function getCategoryName(cat1, cat2, cat3) {
  const key = `${cat1}-${cat2}-${cat3}`;

  //  1 JSON  
  if (categoryData[key]) {
    return categoryData[key]; //  JSON   
  }

  //  2 IndexedDB  
  const cachedValue = await getFromIndexedDB(key);
  if (cachedValue) {
    return cachedValue; //  IndexedDB   
  }

  //  3   API 
  const API_URL = `https://apis.data.go.kr/B551011/KorService1/categoryCode1?MobileOS=ETC&MobileApp=apptest&cat1=${cat1}&cat2=${cat2}&cat3=${cat3}&serviceKey=${API_KEY}&_type=json`;

  try {
    const response = await axios.get(API_URL);
    const items = response.data?.response?.body?.items?.item;

    if (!items || items.length === 0) {
      return "  "; //  API    
    }

    const categoryName = items[0].name; //       

    //  4   IndexedDB 
    await saveToIndexedDB(key, categoryName);

    return categoryName;
  } catch (error) {
    console.error("   :", error);
    return "  ";
  }
}
