import React, { useState, useEffect } from "react";
import axios from "axios";
import "./motelcomparison.css";
import Header from "../../components/header/Header";
import Navbar from "../../components/navbar/Navbar";

const PAGE_SIZE = 10; //   10

const MotelComparison = () => {
  const [motels, setMotels] = useState([]);
  const [selectedMotels, setSelectedMotels] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [searchQuery, setSearchQuery] = useState("");

  useEffect(() => {
    const fetchMotelsData = async () => {
      try {
        const response = await axios.get("/data?fields=,,");

        if (!response.data) {
          console.error("No data received");
          return;
        }

        let motelList = Array.isArray(response.data)
          ? response.data
          : Object.values(response.data);

        setMotels(motelList);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    fetchMotelsData();
  }, []);

  //  / 
  const handleSelect = (motel) => {
    if (selectedMotels.some((m) => m[""] === motel[""])) {
      //     
      setSelectedMotels(
        selectedMotels.filter((m) => m[""] !== motel[""])
      );
    } else if (selectedMotels.length < 2) {
      //    
      setSelectedMotels([...selectedMotels, motel]);
    }
  };

  //   
  const filteredMotels = motels.filter((motel) =>
    motel[""].toLowerCase().includes(searchQuery.toLowerCase())
  );

  //  
  const totalPages = Math.ceil(filteredMotels.length / PAGE_SIZE);
  const currentMotels = filteredMotels.slice(
    (currentPage - 1) * PAGE_SIZE,
    currentPage * PAGE_SIZE
  );

  return (
    <div>
      <Navbar />
      <Header activeItem="comparison" />
      <div className="motel-comparison">
        <h1> </h1>

        {/*   */}
        <input
          type="text"
          placeholder="  ..."
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
        />

        {/*    (1    ) */}
        {selectedMotels.length > 0 && (
          <div className="selected-motels">
            <h2> </h2>
            <div className="selected-motel-list">
              {selectedMotels.map((motel) => (
                <div
                  key={motel[""]}
                  className="motel-item selected"
                  onClick={() => handleSelect(motel)}
                >
                  <img
                    src={`/image/${encodeURIComponent(motel[""])}_1.jpg`}
                    alt={motel[""]}
                    referrerPolicy="no-referrer"
                  />
                  <h2>{motel[""]}</h2>
                  <p>{motel[""]}</p>
                </div>
              ))}
            </div>
          </div>
        )}

        {/*   */}
        <div className="motel-list">
          {currentMotels.map((motel, index) => {
            return (
              <div
                key={index}
                className={`motel-item ${
                  selectedMotels.some((m) => m[""] === motel[""])
                    ? "selected"
                    : ""
                }`}
                onClick={() => handleSelect(motel)}
              >
                <img
                  src={`/image/${encodeURIComponent(motel[""])}_1.jpg`}
                  alt={motel[""]}
                  referrerPolicy="no-referrer"
                />
                <h2>{motel[""]}</h2>
                <p>{motel[""]}</p>
              </div>
            );
          })}
        </div>

        {/*  */}
        <div className="pagination">
          <button
            onClick={() => setCurrentPage((prev) => Math.max(prev - 1, 1))}
            disabled={currentPage === 1}
          >
            
          </button>
          <span>
            {currentPage} / {totalPages}
          </span>
          <button
            onClick={() =>
              setCurrentPage((prev) => Math.min(prev + 1, totalPages))
            }
            disabled={currentPage === totalPages}
          >
            
          </button>
        </div>

        {/*   */}
        {selectedMotels.length === 2 && (
          <div className="comparison-table">
            <h2> </h2>
            <table>
              <thead>
                <tr>
                  <th></th>
                  <th>{selectedMotels[0][""]}</th>
                  <th>{selectedMotels[1][""]}</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td></td>
                  <td>{selectedMotels[0][""]}</td>
                  <td>{selectedMotels[1][""]}</td>
                </tr>
                <tr>
                  <td></td>
                  <td>
                    {selectedMotels[0][""]?.join(", ") || " "}
                  </td>
                  <td>
                    {selectedMotels[1][""]?.join(", ") || " "}
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        )}
      </div>
    </div>
  );
};

export default MotelComparison;
