import React from "react";
import "./review.css";

const Review = ({ comment, sentiment, filter }) => {
  //   sentiment       
  if (filter === "Positive") {
    return (
      <div className="review-card">
        <p className="review-comment">"{comment}"</p>
        <p className="positive">  </p>
      </div>
    );
  }

  if (filter === "Neutral") {
    return (
      <div className="review-card">
        <p className="review-comment">"{comment}"</p>
        <p className="neutral">  </p>
      </div>
    );
  }

  if (filter === "Negative") {
    return (
      <div className="review-card">
        <p className="review-comment">"{comment}"</p>
        <p className="negative">  </p>
      </div>
    );
  }

  return (
    <div className="review-card">
      <p className="review-comment">"{comment}"</p>
      <div className="review-sentiment">
        {sentiment === "Positive" ? (
          <p className="positive">  </p>
        ) : sentiment === "Negative" ? (
          <p className="negative">  </p>
        ) : sentiment === "Neutral" ? (
          <p className="neutral">  </p>
        ) : (
          <p>  </p>
        )}
      </div>
    </div>
  );
};

export default Review;
