import React, { useState } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "./imageslider.css"; // CSS   

const CustomPrevArrow = ({ onClick }) => (
  <button className="custom-arrow custom-prev" onClick={onClick}>
    
  </button>
);

const CustomNextArrow = ({ onClick }) => (
  <button className="custom-arrow custom-next" onClick={onClick}>
    
  </button>
);

const ImageSlider = ({ images }) => {
  const [selectedImage, setSelectedImage] = useState(null); //    

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: true,
    prevArrow: <CustomPrevArrow />,
    nextArrow: <CustomNextArrow />,
    adaptiveHeight: false,
    draggable: false,
    appendDots: (dots) => <div className="custom-dots-container">{dots}</div>,
  };

  const handleImageClick = (image) => {
    setSelectedImage(image); //      
  };

  const handleCloseModal = () => {
    setSelectedImage(null); //  
  };

  return (
    <div className="motel-slider">
      <Slider {...settings}>
        {images.map((image, index) => (
          <div
            key={index}
            className="slide-item"
            onClick={() => handleImageClick(image)}
          >
            <img
              src={image}
              alt={`Motel Image ${index + 1}`}
              className="slide-image"
            />
          </div>
        ))}
      </Slider>

      {/*   */}
      {selectedImage && (
        <div className="image-modal" onClick={handleCloseModal}>
          <div className="image-modal-content">
            <img
              src={selectedImage}
              alt="Selected Motel"
              className="modal-image"
            />
          </div>
        </div>
      )}
    </div>
  );
};

export default ImageSlider;
